<template>
  <div class="cy_win">
        <!-- 示例一 -->
        <div class="line"></div>
        <div class="demoArea" id="demoArea">
             <span class="btn-submit">选择时间</span>
             <input readonly id="year" value="1996">
             <input readonly id="month">
             <input readonly id="date">
        </div>
<div class="codeReadonlyModule" style="height:450px">
<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
  &lt;script type="text/javascript"&gt;
    import area from "cy_static/area";
    var _data = [[], [], []];
    for (var i = 1990; i &lt;= 2000; i++) {
        _data[0].push({text: i + "年",value: i});
    }
    for (var i = 1; i &lt;= 12; i++) {
        _data[1].push({text: i + "月",value: i});
    }
    for (var i = 1; i &lt;= 30; i++) {
        _data[2].push({text: i + "日",value: i});
    }
    var demoArea = area({
        elem: "#demoArea",
        maxNum: 3,
        items: [
            {"id": "#year"}, 
            {"id": "#month"}, 
            {"id": "#date"}
        ],
        isArea: false,// 级别关联
        data: _data
    });
    demoArea.show();
  &lt;/script&gt;
</xmp>
</div>
 
  </div>
</template>

<script>
  import {trim} from "../../../assets/js/codeStyle";
  import area from "cy_static/area";
  export default {
    name: 'example1',
    data () {
      return {
        outWin : null,// 窗口
        cmOptions:{
              readOnly:"true",
              mode : "text/html",  // 模式
              indentUnit : 2,  // 缩进单位，默认2
              smartIndent : true,  // 是否智能缩进
              tabSize : 4,  // Tab缩进，默认4
              showCursorWhenSelecting : true

        }
      }
    },
    mounted(){
      this.example1();
        var m = this;
        ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
            var dom, valDom, index = 0, getDom = function() {
                index++;
                dom = m.$refs[element + index];
                valDom = m.$refs[element + "Val" + index];
            };
            getDom();
            while (dom) {
                dom.codemirror.setValue(trim(valDom.innerHTML));
                getDom();
            }
        });
    },
    methods: {
        example1 () {
            var _data = [[], [], []];
            for (var i = 1990; i <= 2000; i++) {
                _data[0].push({text: i + "年",value: i});
            }
            for (var i = 1; i <= 12; i++) {
                _data[1].push({text: i + "月",value: i});
            }
            for (var i = 1; i <= 30; i++) {
                _data[2].push({text: i + "日",value: i});
            }
            var demoArea = area({
                elem: "#demoArea",
                maxNum: 3,
                items: [
                    {"id": "#year"}, 
                    {"id": "#month"}, 
                    {"id": "#date"}
                ],
                isArea: false,// 级别关联
                data: _data
            });
            demoArea.show();
        }
    }
  }
</script> 
    <style>
    .CodeMirror{height: auto;line-height: 17px;}
    .areaBox .itemBox{cursor: n-resize;}
    </style>
    
  <style scoped>
  .btn-submit{padding: 3px 7px;}
  .cy_win{background-color: #fff;}
  .codeModule {margin: 5px 6px;padding: 5px 8px;}
  .codeModule .codeStyle{font-size:14px;color:#999a9a;line-height:19px}
  .demoArea{overflow:hidden;padding:8px 25px 8px 14px;line-height:1.5;cursor: pointer;background-color: #fff;}
  .demoArea span{float:left;font-size:15px}
  .demoArea input{float:left;width:65px;text-align:center;margin-right:2px;line-height:1.5}
 
  </style>


